
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "msg",
components: {
ButtonTab,
ButtonTabItem,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XButton
},
data: function() {
return {
selectedTabIndex: 0,
hasReadMsg: [],
hasNotReadMsg: []
};
},

created: function() {

var app = this;
this.$http
.get("/messages", {
params: {
accesstoken: this.$store.state.accesstoken
}
})
.then(function(res) {
if (res.data.success) {
console.log(res.data.data);
app.hasReadMsg = res.data.data.has_read_messages;
app.hasNotReadMsg = res.data.data.hasnot_read_messages;
}
});
},
methods:{

markRead(msgid){

var app=this;
this.$http.post('/message/mark_one/'+msgid,{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log('')



app.$router.go(0)
})
},

markAll(){
var app=this;
this.$http.post('/message/mark_all/',{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
if(res.data.success){
console.log('')


app.$router.go(0)
}
})
}
}
};



