
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import {mapMutations} from 'vuex'
export default {
name:'login',
components:{
XInput,
Group,
XButton,
Toast,
Msg
},
data:function(){
return {
accesstoken:'',
isShowToast:false,
toastText:"",
buttons:[
{
type:'primary',
text:'',
link:'/'
}
]
}
},
methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccessToken','mutationAuthorId']),
dologin:function(){

var app=this;
this.$http.post('/accesstoken/',{
accesstoken:this.accesstoken
}).then(function(respones){

if(respones.data.success){

app.mutationLogin(true);
app.mutationLoginname(respones.data.loginname);
app.mutationAccessToken(app.accesstoken)
app.mutationAuthorId(respones.data.id)


window.localStorage.setItem('loginStatus',true);

window.localStorage.setItem('loginname',respones.data.loginname);

window.localStorage.setItem('accesstoken',app.accesstoken);

window.localStorage.setItem('author_id',respones.data.id);

app.$router.push('/user/'+respones.data.loginname)
}
}).catch(function(error){

console.log(error.response)

app.isShowToast=true;
app.toastText=error.response.data.error_msg;
});
}
}
}



