// vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//
Vue.use(Vuex);
//Store
const store = new Vuex.Store({
    // 
    state:{
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''}
          ],
          //actionsheet
          //
          unLoginedMoreMenus:{
            login: ''
          },
            //
          loginedMoreMenus:{
            create:'',
            center:'',
            msg:'',
            logout:''
          },
          // 
          isLogined:false,
          // 
          loginname:'',
          // token
          accesstoken:'',
          // ID
          author_id:'',
          // actionsheet 
          isShowMoreMenus:false,
    },
    // 
    getters:{
        moreMenus:function(state){
            if(state.isLogined){
              return state.loginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
          },
          getTopicDescByTab:function(state){
            return function(tab){
              for(var i=0;i<state.topics.length;i++){
                if(state.topics[i].name==tab){
                  return state.topics[i].desc;
                }
              }
            }
          }
    },
    // 
    mutations,
    // 
    actions

})
//
export default store ;



