
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'

export default {
name:'collect',
props:['loginname'],
components:{
Group,
Cell,
Card
},
data:function(){
return{
collect_topic:''
}
},

beforeRouteEnter:function(to,from,next){
next(function(vm){

vm.$http.get('/user/'+to.params.loginname).then(function(response){

vm.userinfo=response.data.data
});

vm.$http.get('/topic_collect/'+to.params.loginname).then(function(response){
console.log(response.data);
vm.collect_topic=response.data.data;
console.log( vm.collect_topic);
});
})
},
}



