
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:'create',
components:{
Group,
XTextarea,
XButton
},
data:function(){
return {
topic:{
title:'',
content:''
}
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(response){

vm.topic=response.data.data
})
})
},
beforeRouteUpdata:function(to,from,next){
var app=this;
this.$http.get('/topic/'+to.params.id).then(function(response){

app.topic=response.data.data
})
next();
},
methods:{
edittopic(){


this.$router.back()
}
}
}



